@import './../../scss/all.scss';

.biometano {
    background-color: $white;
    min-height: 600px;
    padding: 80px 0;

    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    flex-direction: column;
    overflow: hidden;

    .default-title {
        opacity: 0;
    }

    .container {
        gap: 40px;
        flex-direction: column;
        @include mobile {
            display: flex;
            gap: 30px;
        }
    }

    ul {
        opacity: 0;
        li {
            font-size: 20px;
            letter-spacing: .5px;
            line-height: 28px;
            margin-bottom: 20px;
            font-weight: 500;
        }
    }

    &__list {
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        &__modal {
            max-width: 23%;
            padding: 20px;
            border: 4px solid $color-2;
            border-radius: 16px;

            display: flex;
            align-items: center;
            justify-content: center;
            gap: 10px;

            p {
                font-size: 16px;
                
                b {
                    font-size: 18px;

                }
            }

            img {
                max-height: 100px;

                @include desktop {
                    max-height: 80px
                }
            }
        }
    }




    &.animate {
        .default-title {
            @include fade("in","right", 300px, 3s);
        }

        ul {
            @include fade("in","right", 300px, 3s,200ms);
        }

        .biometano__list__modal:nth-child(1) {
            @include fade("in","right", 300px, 3s,600ms);
        }
        .biometano__list__modal:nth-child(2) {
            @include fade("in","right", 300px, 3s,800ms);
        }
        .biometano__list__modal:nth-child(3) {
            @include fade("in","right", 300px, 3s, 1000ms);
        }
        .biometano__list__modal:nth-child(4) {
            @include fade("in","right", 300px, 3s, 1200ms);
        }
    }

}